import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ActivatedRoute } from '@angular/router';
import { HttpService } from '../shared/http.service';
import Swal from 'sweetalert2';


@Component({
  selector: 'app-myproduct-edit',
  templateUrl: './myproduct-edit.page.html',
  styleUrls: ['./myproduct-edit.page.scss'],
})
export class MyproductEditPage implements OnInit {

  constructor(private http: HttpService, private router: Router, private route: ActivatedRoute) {

    route.params.subscribe(val => {
      this.getCategoryList()
      //this.getSubcategoryList();
      this.getUnit();
      this.LoadReadData();
      this.appendReadData();
      console.log(this.subcategory);


    })


  }


  LoadReadData() {
    this.route.queryParams.subscribe(params => {
      //console.log(params.product_name);
      this.product_name = params.product_name;
      this.tbid_value = params.tbid;
    }
    );
  }


  ngOnInit() {
  }


  subCategoryNotfound: any = false;
  category_tbid: any;
  subcategory_tbid: any;
  popupModel(data) {
    const formdata = new FormData();
    formdata.append("category", data.category);
    this.category_tbid = data.category;
    this.Category = data.category;
    console.log(data.category);
    const c = data.category;

    this.http.get('/read_one_category?c=' + c).subscribe((response: any) => {
      this.category_tbid = response.records.tbid;
      console.log(response.records.tbid);


    }, (error: any) => {
      console.log(error);
    }
    );
  }




  Category_value: any = 1;
  appendReadData() {
    const p = this.product_name;
    //console.log(p);
    this.http.get('/read_one_product?p=' + p).subscribe((response: any) => {

      this.description = response.records.description;
      this.Category = response.records.category;
      this.subcategory = response.records.subcategory;
      this.productname = response.records.product_name;
      this.cost = response.records.cost;
      this.DisplayImage = response.records.product_image;
      this.tbid = response.records.tbid;
      this.weight = response.records.weight;
      this.unit = response.records.unit;
      console.log(response.records);

    }, (error: any) => {
      console.log(error);
    }
    );
  }

  product_name: any = "";
  tbid: any = "";
  DisplayImage: any = "";
  tbid_value: any = "";
  displayreaddata = [];
  readunit: [];
  showProducts = [];
  public date: string = new Date().toISOString();
  Category: any = '';
  subcategory: any = '';
  productname: any = '';
  description: any = '';
  cost: any = '';
  showimage = '';
  showImageDisplay = false;
  categoryName: any = '';
  subcategoryName: any = '';
  categoryList: any = [];
  subcategoryList: any = [];
  hideImageDisplay = true;
  PopupModel: any = false;
  subcategoryPopupModel: any = false;
  readunitvalue: any = "";
  weight: any;
  unit: any;
  showselecteditems() {
    const updateItems = {
      tbid: this.tbid_value,
      category: this.Category,
      subcategory: this.subcategory,
      product_name: this.productname,
      description: this.description,
      cost: this.cost + this.readunitvalue
    }
    console.log(updateItems);

  }

  MediaFileSelected(event) {
    this.selectedFile = event.target.files[0] as File;

  }
  selectedFile: File = null;
  image: any;

  onClickSubmit(data) {
    this.image = this.selectedFile;
    console.log(this.image)
    const formdata = new FormData();
    formdata.append("tbid", this.tbid_value)
    formdata.append("category", this.category_tbid);
    formdata.append("subcategory", this.subcategory_tbid);
    formdata.append("product_name", data.product_name);
    formdata.append("cost", data.cost);
    formdata.append("unit", data.unit);
    formdata.append("description", data.description);
    formdata.append("product_image", this.image);
    formdata.append("weight", data.weight)

    // console.log('formData: ', formdata.getAll('category'), formdata.getAll('product_image'));

    // var body = "category = " + data.category + "subcategory = " + data.subcategory + "product_name = " + data.product_name + "cost = " + data.cost + "unit = " + data.unit + "product_image = " + this.image + "description = " + data.description;
    // console.log(body);

    this.http.postFormData("/update_product", formdata).subscribe((response: any) => {
      console.log(response);
      const Toast = Swal.mixin({
        toast: true,
        position: 'top-end',
        showConfirmButton: false,
        timer: 1000,
        timerProgressBar: true,
        didOpen: (toast) => {
          toast.addEventListener('mouseenter', Swal.stopTimer)
          toast.addEventListener('mouseleave', Swal.resumeTimer)
        }
      })

      Toast.fire({
        icon: 'success',
        title: 'Updated successfully'
      })
      this.router.navigate(['/myproducts']);

    }, (error: any) => {
      console.log(error);
    }
    );
  }

  backToprivious() {
    this.PopupModel = false;
    this.Category = ''

  }

  deleteProduct() {
    const obj = {
      tbid: this.tbid_value
    }
    this.http.post('/delete_product', obj).subscribe((response: any) => {
      //console.log(response);
      if (response.success == "true") {
        const Toast = Swal.mixin({
          toast: true,
          position: 'top-end',
          showConfirmButton: false,
          timer: 1000,
          timerProgressBar: true,
          didOpen: (toast) => {
            toast.addEventListener('mouseenter', Swal.stopTimer)
            toast.addEventListener('mouseleave', Swal.resumeTimer)
          }
        })

        Toast.fire({
          icon: 'success',
          title: 'Deleted successfully'
        })
        this.router.navigate(['/myproducts'])
      }


    }, (error: any) => {
      //console.log(error);
    }
    );

  }


  NavigatetoNotification() {
    this.router.navigate(['/notification'])
  }


  ScBackToprivious() {
    this.subcategoryPopupModel = false;
    this.subcategory = ''

  }

  addproduct() {

    this.router.navigate(['/myproducts'])
  }
  createCategory() {
    this.PopupModel = false;
    const catData = {
      category_name: this.categoryName,
      created_at: this.date
    }

    this.http.post('/create_category', catData).subscribe((response: any) => {
      //console.log(response);

      if (response.success == "true") {
        this.Category = ''
        this.categoryName = ''
        this.getCategoryList()


      }

    }, (error: any) => {
      //console.log(error);
    }
    );

  }


  getUnit() {
    this.http.get('/read_unit').subscribe((response: any) => {
      //console.log(response.records);
      this.readunit = response.records;


    }, (error: any) => {
      //console.log(error);
    }
    );

  }


  createSubcategory() {
    this.subcategoryPopupModel = false;
    const subcatData = {
      category: this.Category,
      subcategory_name: this.subcategoryName

    }

    this.http.post('/create_subcategory', subcatData).subscribe((response: any) => {
      //console.log(response);

      if (response.success == "true") {
        //console.log("test");
        this.Category = ''
        this.subcategory = ''
        this.subcategoryName = ''
        //this.getSubcategoryList()


      }

    }, (error: any) => {
      console.log(error);
    }
    );

  }

  getCategoryList() {

    this.http.get('/read_all_category',).subscribe((response: any) => {
      //console.log(response.records);

      this.categoryList = response.records
      console.log(response.records);
      //console.log(this.categoryList);


    }, (error: any) => {
      //console.log(error);
    }
    );
  }

  // getSubcategoryList() {
  //   this.http.get('/read_all_subcategory',).subscribe((response: any) => {
  //     //console.log(response.records);
  //     this.subcategoryList = response.records
  //     //console.log(response.records);
  //     //console.log(this.subcategoryList);

  //   }, (error: any) => {
  //     console.log(error);
  //   }
  //   );
  // }



  backToMyproducts() {
    this.router.navigate(['/myproducts'])
  }

  isvisible: any = false;

  hidepopup() {
    this.isvisible = false;
  }
  showPopup() {
    this.isvisible = true;
    this.http.get('/product_image_list').subscribe((response: any) => {
      console.log(response);
      this.showProducts = response.records;

    }, (error: any) => {
      console.log(error);

    }

    )
  }
  displayImage(i) {
    this.showImageDisplay = true;
    this.showimage = this.showProducts[i];
    console.log(this.showimage);
    this.isvisible = false;
    this.hideImageDisplay = false;
  }





  addnewcategorypopup: any = false;
  addnewsubcategorypopup: any = false;

  hideaddnewcategorypopup() {
    this.addnewcategorypopup = false;

  }
  showaddnewcategorypopup() {
    this.addnewcategorypopup = true;

  }

  showaddnewsubcategorypopup() {
    this.addnewsubcategorypopup = true;
  }

  hideaddnewsubcategorypopup() {
    this.addnewsubcategorypopup = false;
  }

  categorysubmit() {
    const obj = {
      category: this.category_tbid
    }
    console.log(obj);

    this.http.post('/read_subcategory', obj).subscribe((response: any) => {
      console.log(response.records);
      this.subcategoryList = response.records
      console.log(this.subcategoryList);
      this.subCategoryNotfound = false;

    }, (error: any) => {
      console.log(error);
      this.subCategoryNotfound = true;
      this.subcategoryList = [];
    }
    );
  }
  subcategorysubmit(value) {
    console.log(value.subcategory);
    this.subcategory = value.subcategory;
    const s = value.subcategory;
    this.http.get('/read_one_subcategory?s=' + s).subscribe((response: any) => {
      this.subcategory_tbid = response.records.tbid;
    }, (error: any) => {
      console.log(error);
    }
    );
  }

}
